.twoColumnGrid{
  display: grid;
  grid-template-columns: auto auto;
}



@media only screen and (max-width:10000px){
  .typeLine{
    text-transform:capitalize;
    font-weight: 900;

  }
}

@media only screen and (max-width:500px){
  .typeLine{
    text-transform:capitalize;
    font-weight: 900;
    font-size: 10px;
  }

}

.playerDisplayLine{
  text-transform: capitalize;
  
  display: inline;
  font-weight: 900;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-style: italic;
  cursor:pointer;
  color:#080808;
  text-decoration:none;
}



.loading{
    z-index: 10000;
    position: fixed;
    top:0;
    animation-name: fadein;
    animation-duration: 1s;
}




.grid-container-deckPage {
  display: grid;
  grid-template-columns: auto auto auto;
}



.grid-container-deckPage2 {
  display: grid;
  grid-template-columns: auto;
}

.fadeInDude{
  animation-name: fadein;
  animation-duration: 1s;
}

.deckPageSelect{
  background-color: #485761;
  color:snow;
  border: 2px;
  padding: 6px;
  border-width: 2px;
  border-color: black;
  margin: 10px;
}

.deckPageFixedImage{
  
  border-radius: 5px;
  border: 5px;
}

.deckPage .tooltip {
    display: inline;
}





.generalLink{
  text-decoration: none;  
}

.generalLink:hover{
  font-weight: 700;
}


.deckPageButton{
    width: 31.5%;
    display: inline-block;
    background-color: #288745;
    color: white;
    font-size: 22px;
    text-align: center;
    margin:1px;
    padding: 10px;
    border-radius: 4px;
}

.deckPageButtonDislabled{
    background-color: rgba(28,87,45,0.9);
    
}
.grid-container-deckPage3 {
  display: grid;
  grid-template-columns: auto auto auto auto auto;
 
  
}

.deckPageButtonOrange{
  width: 31.5%;
  display: inline-block;
  background-color: #FF851B;
  color: white;
  font-size: 22px;
  text-align: center;
  margin:1px;
  padding: 10px;
  border-radius: 4px;
}

.deckPageButtonRed{
  width: 31.5%;
  display: inline-block;
  background-image: linear-gradient(red,darkred);
  color: white;
  font-size: 22px;
  text-align: center;
  margin:1px;
  padding: 10px;
  border-radius: 4px;
}

.deckPageGlobal nav{
    cursor: pointer;
}

.deckPageGlobal a:hover{
    color:cornflowerblue;
}

@media only screen and (max-width: 900px) {
    .deckPageButton {
      display: block;
      width: 100%;
    }
    .deckPageButtonOrange{
      display: block;
      width: 100%;
    }
    .deckPageButtonRed{
      display: block;
      width: 100%;
    }

    
    
}



.cardTextPresentationLine{
  display: inline;
}

.deckPageButton:hover{
    background-color: #38b760;
    cursor:pointer;
}

.deckPageButtonOrange:hover{
  background-color: #FF851B;
  opacity: 0.9;
  cursor:pointer;
}

.deckPageButtonRed:hover{
  background-image: linear-gradient(red,darkred);
  cursor:pointer;
}

.deckAreaDP{
  margin-top: 10px;     
 
}

.deckAreaDP select{
  display: block;
  width: 100%;
  border-radius: 1px;
  background-color: white;
  color: black;
  font-size: 20px;
  text-align: center;
  padding-top:2px;
  padding-bottom: 2px;
  margin-bottom: 10px;
}

.deckAreaDP .CommanderSelection{    
  font-size: 20px;
  
}

.deckAreaDP .CardInput{
  width: 100%;
  height: 30px;
  resize: none;
  font-size: 20px;
  padding-left: 5px;
  white-space: nowrap;
  margin-bottom: 10px;
}
.deckAreaDP label{
  display: block;
  width: 100%;
  color: white;
  background-color: #333;
  text-align: center;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  padding-top: 5px;
  padding-bottom: 5px;
  font-size: 20px;    
}

.deckAreaDP input{  
  height: 30px;
  font-size: 20px;
  border-radius: 0px;
  width: 100%;
  padding-left: 4px;
}
@media only screen and (max-width: 900px){
  .deckAreaDP{
      margin-top: 20px;
      margin-left: 20px;
      margin-right: 20px;
  }
}

.decksPageOPContainer{
  position: relative;
  width: 50%;
}


.decksPageOPContainer:hover .decksPageOPMiddle {
 opacity: 1;
}

.decksPageOPMiddle {
  transition: .5s ease;
  opacity: 1;
  position: absolute;
  top: 30%;
  left: 45%;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  text-align: center;
}

.decksPageOPImageCenterText {
  background-color: #288745;
  color: white;
  padding: 3px 3px;
  
  border-radius: 25px;
}

.decksPageOPImageCenterText:hover {
  background-color: #38b760;

}
.flipEffectDPOP{
  animation-name: flip;
  animation-duration: 1s;
}

@keyframes flip {
  10% {
    transform: rotateY(20deg);
  }

  20% {
    transform: rotateY(50deg);
  }

  30% {
    transform: rotateY(80deg);
  }

  40% {
    transform: rotateY(130deg);
  }
  50% {
    transform: rotateY(180deg);
  }

  


}